import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import { JournalEntry } from './JournalEntry'

export const JournalEntries = () => {

	const {notes} = useSelector(state => state.notes)

	useEffect(() => {
	}, [notes]);

	return (
		<div
			className="journal__entries"
		>
			{
				notes.map( note => (
					<JournalEntry
						key={note._id}
						{...note}
					/>
				))
			}
		</div>
	)
}
